.App {
  height: 565px;
  background-size: cover;
  padding-top: 25vh;
  transition: background-color 0.5s ease;
}

.box {
  border: 3px solid #061d2c;
border-radius: 23px;
  padding: 8px;
  border-radius: 12px;
  
  
}

.wrapper {
  margin: 0 auto;
  width: 500px;
  height: 200px;
  
}

.quote {
  text-align: center;
  margin-bottom: 40px;
  
}

.bottom {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 40px;
  padding-right: 20px;
}

button {
  border: none;
  padding: 12px 24px;
  border-radius: 50px;

  font-weight: 600;
  color: #474747;
  background-color: #00ffbf54;

  cursor: pointer;
}

.tweet-quote {
  padding: 5px;
  border: 2px solid #061d2c;
border-radius: 10px;
}
